import React from "react";
import {BrowserRouter as Router, Switch,Route,Link} from "react-router-dom";
import { Navbar, Nav, NavDropdown, Container } from "react-bootstrap";
import 'bootstrap/dist/css/bootstrap.min.css';
import Home  from "./Home";
import PageNotFound from "./PageNotFound";
import Autos from "./Autos";
import Motos from "./Motos";
import Camiones from "./Camiones";
import Login from "./Login";
import About from "./About";
import Contactenos from "./Contactenos";


const Menu = () =>{
    return(
<Router>
<Navbar collapseOnSelect expand="lg" className="colorMenu" variant="dark">
  <Container>
  <Navbar.Brand href="./">Inicio</Navbar.Brand>
  <Navbar.Toggle aria-controls="responsive-navbar-nav" />
  <Navbar.Collapse id="responsive-navbar-nav">
    <Nav className="me-auto">
      <NavDropdown title="Vehiculos" id="basic-nav-dropdown">
      <NavDropdown.Item href="./Autos/">Autos</NavDropdown.Item>
      <NavDropdown.Item href="./Motos/">Motos</NavDropdown.Item>
      <NavDropdown.Item href="./Camiones/">Camiones</NavDropdown.Item>
      <NavDropdown.Item href="./Camionetas/">Camionetas</NavDropdown.Item>
      </NavDropdown>
    </Nav>
    <Nav>
    <Nav.Link href="./Login/">Registrarse</Nav.Link>
    <Nav.Link href="./About/">Acerca de</Nav.Link>
    <Nav.Link href="./Contactenos/">Contactenos</Nav.Link>
    </Nav>
  </Navbar.Collapse>
  </Container>
</Navbar>
 
  <Switch>
    <Route path="./" exact component={Home}/>
    <Route path="./Autos/" exact component={Autos}/>
    <Route path="./Motos/" exact component={Motos}/>
    <Route path="./Camiones/" exact component={Camiones}/>
    <Route path="./Login/" exact component={Login}/>
    <Route path="./About/" exact component={About}/>
    <Route path="./Contactenos/" exact component={Contactenos}/>
    <Route component={PageNotFound}/>
  </Switch>
</Router>
)
};



export default Menu